body {
  font-family: Arial, sans-serif;
  background-color: #f5f5f5;
  margin: 0;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

#container {
  width: 1700px;
  height: 1000px;
  position: relative;
}
#title {
  color: rgba(0, 255, 217, 0.555); /* צבע הכתובת */
  font-size: 36px; /* גודל הכתובת */
  text-align: center; /* מרכוז הטקסט */
  position: absolute; /* מיקום יחסי */
  top: 20px; /* מרחק מהחלון למעלה */
  left: 50%; /* מרכוז לרוחב */
  transform: translateX(-50%); /* הזזת הטקסט חזרה לאמצע */
  font-size: 50px;
}

table {
  border-collapse: collapse;
  margin: 0 auto;
  border: 2px solid #333;
}

td, tr {
  border: 1px solid #999;
  position: relative;
}

input {
  width: 50px;
  height: 50px;
  font-size: 24px;
  text-align: center;
  border: none;
  outline: none;
  background-color: #f9f9f9;
}

input[value=""] {
  background-color: #fff;
}

tr:nth-child(3n), tr:nth-child(3n+1) {
  border-bottom: 3px solid #333;
}

td:nth-child(3n), td:nth-child(3n+1) {
  border-right: 3px solid #333;
}

.buttons {
  text-align: center; /* יישור הכפתורים לאמצע */
}

.button {
  border-radius: 2px;
  background-color: #4CAF50; /* ירוק */
  color: white;
  width: 150px;
  height: 50px;
  margin: 20px 10px; /* מרווח בין הכפתורים */
  display: inline-block; /* להציג את הכפתורים בשורה אחת */
  text-align: center;
  line-height: 50px; /* ליישור הטקסט לאורך */
  text-decoration: none;
  transition: background-color 0.3s ease; /* אנימציה שמשנה צבע בעת העברת העכבר */
  cursor: pointer; /* עכבר מצביע כפתור */
}

.button:hover {
  background-color: #45a049; /* צבע ירוק כהה בעת עברת עכבר */
}

body {
  background-image: url("p.jpg");
  background-size: cover; /* להתאים את גודל התמונה לרקע */
  background-repeat: no-repeat; /* אי אפשרות לחזור על תמונה */
  background-position: center; /* מיקום התמונה במרכז */
}
